*,*::before,*::after{
  box-sizing: border-box;
  font-family: cursive;
}
body{
  margin: 0;
  background-image: linear-gradient(to right,lightgreen,lightblue);
}
.App {
  text-align: center;
}

.gcalc{
  display: grid;
  margin-top: 1rem;
  grid-template-columns: repeat(4,5rem);
  grid-template-rows: minmax(7rem,auto) repeat(5,5rem);
  justify-content: center;
  background-image: linear-gradient( to top ,#2E3192,#1BFFFF);
  padding: 10px;  
}
.gcalc .optbtn{
  background-color: cornflowerblue;
  border: 3px solid lightgreen;
}
.gcalc .numbtn{
  background-color: darkcyan;
  border: 3px solid skyblue;
}
.gcalc > button{
  cursor: pointer;
  font-size: 2rem;
  border: 5px solid paleturquoise;
  outline: none;
  background-color: dodgerblue;
}
.gcalc > button:hover,
.gcalc > button :focus{
  background-color: white;
}
.sptwo{
  grid-column: span 2;
}
.otpt{
grid-column: 1 / -1;
background-color: black;
display: flex;
flex-direction: column;
align-items: flex-end;
justify-content: space-around;
padding: .075rem;
word-wrap: break-word;
word-break: break-all;
border: 5px solid grey;
}
.otpt .preop{
color:wheat;
font-size: 1.5rem;
}
.otpt .curop{
color:white;
font-size: 2.5rem;
}

